* {
  margin: 0;
  padding: 0;
}

.App {
  background-color: rgb(0, 0, 0);;
  min-height: 100vh;
  overflow-x: hidden;
  font-family: 'Roboto', sans-serif;
  background: linear-gradient(to right, rgba(38, 24, 7, 0.85), rgba(21, 17, 8, 0.85)), url(https://grainy-gradients.vercel.app/noise.svg);
}

.navigation {
  z-index: 900;
}

.primary-button {
  background-color: #ac7d49;
  padding: 4px 16px;
  border-radius: 3px;
  border: none;
  color: #fff;
  cursor: pointer;
}

.secondary-button {
  background-color: transparent;
  padding: 4px 16px;
  border-radius: 3px;
  border: 1px solid #fff;
  color: #fff;
  cursor: pointer;
}

button:disabled.primary-button  {
  background-color: silver;
  color: rgb(70, 70, 70);
}

.secondary-button:active {
  background-color: #ac7d49;
}

.secondary-button:focus {
  background-color: #ac7d49;
}

button:focus{
  outline: none;
  box-shadow: none;
}

.cart-icon {
  font-size: 32px !important;
  margin-right: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  /* padding-top: 3px; */
  cursor: pointer;
  border-radius: 100%;
}


.sub-hero {
  background: linear-gradient(45deg, rgba(15, 15, 15, 0.95), rgba(22, 7, 2, 0.95)), url('/src/media/images/coffee-green.jpg');
  object-fit: contain;
  background-size: cover;
  background-position: center;
  box-shadow: inset 0 0 100px black;
}

.catalog-section {
  min-height: 100vh;
  /* background-color: rgb(23, 25, 30); */
  /* background: linear-gradient(to right, rgba(250, 163, 0, 0.3), rgba(250, 163, 0, 0.3)), url(https://grainy-gradients.vercel.app/noise.svg); */
}

.menu-tabs span {
  border: 1px solid silver;
  padding: 6px 16px;
  border-radius: 3px;
  margin-right: 10px;
  cursor: pointer;
}

.menu-tabs span.active {
  background-color: #ac7d49;
  cursor: pointer;
}

button.amount-handler {
  width: 40px;
  height: 40px;
  cursor: pointer;
  background-color: transparent;
  color: #fff;
  border: 1px solid #fff;
  border-radius: 5px;
}

button.amount-handler:active {
  background-color: #cda77c;
}

button.add-to-cart {
  width: 100px;
  height: 40px;
  cursor: pointer;
  background-color: transparent;
  color: #fff;
  border: 1px solid #fff;
  border-radius: 5px;
}

.modal-window {
  z-index: 1000;
}

.btn-close {
  display: none;
}

@media screen and (max-width: 468px){
  .navigation {
    padding: 10px 20px !important;
  }

  #hero h1 {
    font-size: 28px;
  }

  #hero h5 {
    font-size: 16px;
  }

  .sub-hero .container .row {
    display: flex !important;
    flex-direction: column !important;
  }

  .sub-hero {
    padding-top: 0 !important;
    padding-bottom: 100px !important;
    height: 45vh !important;
  }

  .sub-hero img {
    display: none;
  }

  #hero > div.d-flex.sub-hero.align-items-center > div {
    padding-top: 0 !important;
    margin-top: 0 !important;
    padding-bottom: 100px !important;
  }

  #hero > div.d-flex.sub-hero.align-items-center > div > div > div.col.m-0.p-0 > div {
    display: flex !important;
    justify-content: center !important;
    padding-top: 20px !important;  
  }

  #catalog-section {
    max-width: 468px !important;
  }

  #catalog-section > div.row.pl-3.mt-3 > div.col.d-flex.flex-column.p-0 > div > div.pl-3.d-flex.flex-column.justify-content-between > div:nth-child(1) > p {
    width: 220px !important;
  }

  #catalog-section > div.row.pl-3.mt-3 > div.col-2.d-flex.flex-column.p-0 {
    display: grid !important;
    grid-template-columns: repeat(2, 1fr) !important;
  }

  #catalog-section > div.row.pl-3.mt-3 {
    display: flex !important;
    flex-direction: column !important;
  }

  #catalog-section > div.row.pl-3.mt-3 > div.col-2.d-flex.flex-column.p-0 > span {
    display: flex !important;
    width: 150px !important;
  }

  #catalog-section > div.row.pl-3.mt-3 > div.col.d-flex.flex-column.p-0 > div {
    border: none !important;
  }

  #catalog-section > div.row.pl-3.mt-3 > div.col.d-flex.flex-column.p-0 > div {
    padding: 0 !important;
    margin: 20px 0 !important;
    display: flex !important;
  }

  #catalog-section > div.row.pl-3.mt-3 > div.col.d-flex.flex-column.p-0 > div > div:nth-child(1) {
    width: auto !important;
    height: auto !important;
  }

  #catalog-section > div.row.pl-3.mt-3 > div.col.d-flex.flex-column.p-0 > div > div:nth-child(1) > img {
    width: 100px !important;
    height: 180px !important;
  }

  #catalog-section > div.row.pl-3.mt-3 > div.col.d-flex.flex-column.p-0 > div > div.pl-3.d-flex.flex-column.justify-content-between h2 {
    font-size: 24px !important;
  }

  #catalog-section > div.row.pl-3.mt-3 > div.col.d-flex.flex-column.p-0 > div > div.pl-3.d-flex.flex-column.justify-content-between {
    font-size: 14px !important;
    max-width: 220px !important;
  }

  button.amount-handler {
    width: 30px !important;
    height: 30px !important;
  }

  #catalog-section > div.row.pl-3.mt-3 > div.col.d-flex.flex-column.p-0 > div > div.pl-3.d-flex.flex-column.justify-content-between > div:nth-child(2) > div > span.ml-3 > button {
    width: auto !important;
    padding: 4px 5px !important;
  }

}


